import styled, { css } from 'styled-components';

import { themeVal, glsp } from '@theme/utils';

const renderBadgeVariation = (props = {}) => {
  const { variation = 'base' } = props;

  return css`
    border-color: ${themeVal(['color', variation])};
  `;
};

export const createBadgeStyles = (props) => css`
  display: inline-block;
  border-radius: ${themeVal('shape.rounded1')};
  border-width: 1px;
  border-style: solid;
  text-transform: uppercase;
  text-align: center;
  padding: ${glsp(0.25, 0.5)};
  color: ${({ overDark }) =>
    themeVal(overDark ? 'color.surface' : 'color.base')};
  font-size: 0.875rem;
  line-height: 1rem;

  /* Variation */
  ${renderBadgeVariation(props)}
`;

export const Badge = styled.small`
  ${createBadgeStyles}
`;
