const renderCollectionFoldType = (props = {}) => {
  const { type } = props;
  switch (type) {
    case 'genesis':
      return css`
        background: ${themeVal('color.base')};
      `;
    case 'unleashed':
      return css`
        background: ${themeVal('color.neut1')};
      `;
    case 'pups':
      return css`
        background: ${themeVal('color.base')};
      `;
  }
};

