import React from 'react';
import T from 'prop-types';
import SimpleReactLightbox, {
  SRLWrapper,
  useLightbox
} from 'simple-react-lightbox';
import { createGlobalStyle } from 'styled-components';
import { themeVal, glsp } from '@theme/utils';

const GalleryOverrideStyles = createGlobalStyle`
  .SRLImage {
    border-radius: ${themeVal('shape.rounded2')};
  }

  button.SRLNextButton,
  button.SRLPrevButton,
  button.SRLCloseButton {
    background: none;
    height: 4rem;
    width: 4rem;
    border-radius: ${themeVal('shape.ellipsoid')};
    border: 1px solid ${themeVal('color.surface-200a')};

    &:hover {
      border-color: ${themeVal('color.surface')};
    }
  }

  button.SRLNextButton {
    right: ${glsp(4)};
  }

  button.SRLPrevButton {
    left: ${glsp(4)};
  }

  .SRLControls.SRLControls {
    top: ${glsp()};
    right: ${glsp()};
  }

  button.SRLCloseButton {
    height: 3rem;
    width: 3rem;

    > div {
      padding: 12px;
    }
  }
`;

const options = {
  settings: {
    slideAnimationType: 'slide',
    disablePanzoom: true
  },
  buttons: {
    showAutoplayButton: false,
    // showCloseButton: false,
    showDownloadButton: false,
    showFullscreenButton: false,
    showThumbnailsButton: false
  },
  caption: {
    showCaption: false
  },
  thumbnails: {
    showThumbnails: false
  },
  progressBar: {
    showProgressBar: false
  }
};

const GallerySRL = (props) => {
  const { children, elements } = props;
  const { openLightbox, closeLightbox } = useLightbox();
  return (
    <div>
      {children({ openLightbox, closeLightbox })}
      <GalleryOverrideStyles />
      <SRLWrapper elements={elements} options={options} />
    </div>
  );
};

GallerySRL.propTypes = {
  children: T.func.isRequired,
  elements: T.array.isRequired
};

export default function Gallery(props) {
  return (
    <SimpleReactLightbox>
      <GallerySRL {...props} />
    </SimpleReactLightbox>
  );
}
